<template>
    <div style="display: inline;">
        <div class="new-game-box">
            <div class="title">新游戏</div>
            <div class="level-buttons">
                <button class="level-button">简单</button>
                <button class="level-button">普通</button>
                <button class="level-button">一般</button>
                <button class="level-button">困难</button>
            </div>
        </div>
        <div class="action-buttons">
            <button class="action-button">显示答案</button>
            <button class="action-button">重新开始</button>
            <button class="action-button">返回主页面</button>
        </div>
    </div>

    <div class="container">

        <div v-for="(sudokuGrid, gridIndex) in sudokuGrids" :key="gridIndex" class="sudoku">
            <div v-for="(row, rowIndex) in sudokuGrid" :key="rowIndex" class="row"
                :class="{ 'bold-row': rowIndex % 3 === 0 }">
                <div v-for="(cell, colIndex) in row" :key="colIndex" class="cell"
                    :class="{ 'bold-cell': colIndex % 3 === 0 }">
                    {{ cell === 0? '': cell}}
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import axios from 'axios';

export default {
    data() {
        return {
            sudokuGrids: [],
            sudoAnswer: [],
            // sudoList:[]
        };
    },
    created() {
        const level = this.$route.query.level;
        this.level = level
        console.log(level);

    },
    mounted() {
        const data = {
            params: {
                level: this.level
            }
        }
        axios.get('http://43.138.171.179:9000/sudoku', data).then((response) => {
            // console.log(response.data.data);
            this.sudoList = response.data.data;
            this.sudoList.forEach((obj) => {
                this.sudokuGrids.push(obj.finalResult);
            });
            console.log(this.sudoList);
        })

    }

};
</script>
  
<style>
.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #b2ffaf;
    opacity: 0.7;
    margin-top: 20px;

}

.new-game-box {
    /* margin-top: 500px; */
    width: 600px;
    height: 50px;
    margin: 10px;
    border: 2px solid #000;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: auto;
    justify-content: center;
}

.title {
    font-size: 20px;
    font-weight: bold;
    margin-right: 10px;
    margin-left: 5px;
}

.level-buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.level-button {
    margin-right: 10px;
}

.action-buttons {
    margin-top: 20px;
    width: 600px;
    height: 50px;
    margin: 10px;
    border: 2px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.action-button {
    margin: 5px;
}

.sudoku {
    width: 273px;
    margin: 10px;
    /* 添加外边距来创建间隔 */
    border: 2px solid #000;
    box-sizing: border-box;
}


.row {
    display: flex;
}

.cell {
    width: 30px;
    height: 30px;
    border: 1px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box
}


.bold-row {
    border-top: 2px solid #000;
}

.bold-cell {
    border-left: 2px solid #000;
}
</style>